.sim-sandbox .button-group {
  margin-bottom: 10px;
  position: relative;
}

.sim-sandbox .button-group-button-container {
  margin-bottom: 0;
  overflow: hidden;
}

.sim-sandbox .button-group-label, .sim-sandbox .button-group-label2 {
  margin: 2px 4px;
  font-size: 15px;
  font-weight: bold;
  line-height: 1.1em;
}

.sim-sandbox .button2 {
  font: inherit;
  cursor: pointer;
  text-align: center;
  -o-transition: all .1s;
  height: 24px;
  margin: 1px 4px;
  padding: 1px 4px;
  font-size: 15px;
  font-weight: bold;
  line-height: 20px;
  transition: all .1s;
  top: 0;
}

.sim-sandbox .button {
  float: left;
  height: 20px;
  position: relative;
  overflow: hidden;
}

.sim-sandbox .button-group .smaller {
  font-size: 80%;
}

.sim-sandbox .button svg {
  font-size: 20px;
}

.sim-sandbox .button:hover {
  background: #ddd;
  border-color: #999;
}

.sim-sandbox .button:active {
  background: #333;
  border-color: #000;
}

.sim-sandbox .button[on="yes"] {
  color: #000;
  background: #ccc;
}

.sim-sandbox .button[on="yes"]:hover {
  background: #ddd;
}

.button[on="yes"]:active {
  background: #111;
}

.sim-sandbox .button2 {
  color: #888;
  background: #fff;
  border: 1px solid #888;
}

.darkMode .sim-sandbox .button2:hover {
  background: #222;
  border-color: #777;
}

.darkMode .sim-sandbox .button2:active {
  background: #ddd;
  border-color: #fff;
}

.darkMode .sim-sandbox .button:hover {
  background: #222;
  border-color: #777;
}

.darkMode .sim-sandbox .button:active {
  background: #ddd;
  border-color: #fff;
}

.darkMode .sim-sandbox .button[on="yes"] {
  color: #fff;
  background: #444;
}

.darkMode .sim-sandbox .button[on="yes"]:hover {
  background: #222;
}

.darkMode .button[on="yes"]:active {
  background: #eee;
}

.darkMode .sim-sandbox .button2 {
  color: #888;
  background: #000;
  border: 1px solid #888;
}

.label-tip {
  z-index: 4;
  background-color: #fff;
  border: 1px solid #000;
  padding: 4px;
  position: absolute;
  top: 20px;
  left: 0;
}

.darkMode .label-tip {
  background-color: #555;
  border: 1px solid #fff;
}

body {
  color: #222;
  background: #f5f5f5;
}

div.screenWrap {
  background: #fff;
  position: relative;
}

div.screenWrap, canvas.maps, div.svgDiv, div.mSvgDiv {
  border: 1px solid #888;
  margin: 4px;
}

.background {
  z-index: 0;
  position: absolute;
}

div.tooltips {
  z-index: 2;
  position: absolute;
}

.foreground {
  z-index: 1;
  position: absolute;
}

div.tooltips[data-cursor="grab"] {
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: grab;
}

div.tooltips[data-cursor="grabbing"] {
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grabbing;
}

div.tooltipBox {
  -webkit-user-select: none;
  user-select: none;
}

button {
  margin: 2px;
}

textarea {
